.calendar {  
  color: #444;
  padding-top: 10px;
  padding-bottom: 10px;
}

.calendar-header {
  color: #444;
  text-align: left;
  padding-left: 15px;
  padding-top: 20px;
  font-weight: 500;
}

.calendar-header_month {
  color: #444;
  padding-left: 5px;
  text-transform: uppercase;
  font-size: 14px;
}

.calendar-header_date {
  padding-left: 1px;
  font-size: 20px;
}

.slider-body {
  background-color: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin: 3px;
  position: relative;
  top: 0;
  left: 0;
}
