body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {  
  border-bottom: 1px solid rgba(0, 0, 0, 0.03);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #444;
  font-size: 14px;
}

.App-header_title {
	color: rgb(51, 114, 226);
	font-size: 16px;
	padding: 10px; 
	margin: 0;
}

.App-header_title__departure {
	padding-right: 10px;
}

.App-header_title__arrival {
	padding-left: 7px;
}

.App-header_subtitle {
	font-size: 12px;
	color: rgba(0, 0, 0, .4);
}

.App-header_back {
	position: absolute;
	top: 0;
	left: 0;
	padding: 9px; 
	margin: 0;
	font-size: 16px;
	color: rgb(51, 114, 226);
}

.slider {
	color: #444;
	display: flex;
	justify-content: space-around;
	margin: 0;
	padding: 0;
	font-size: 14px;
}

.slider-item {
	list-style: none;
	position: relative;
}

.slider-item_day {
	text-transform: uppercase;
	font-size: 14px;
}

.selected-date {
	color: rgb(51, 114, 226);
	position: relative;
}

.selection-circle {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.selection-circle:after {
	border-radius: 50%;
	border: 1px solid blue;
	content: '';
	background-color: rgb(239, 242, 253);
	display: inline-block;
	width: 30px;
	height: 30px;	
	z-index: -1;
}

.calendar {  
  color: #444;
  padding-top: 10px;
  padding-bottom: 10px;
}

.calendar-header {
  color: #444;
  text-align: left;
  padding-left: 15px;
  padding-top: 20px;
  font-weight: 500;
}

.calendar-header_month {
  color: #444;
  padding-left: 5px;
  text-transform: uppercase;
  font-size: 14px;
}

.calendar-header_date {
  padding-left: 1px;
  font-size: 20px;
}

.slider-body {
  background-color: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin: 3px;
  position: relative;
  top: 0;
  left: 0;
}


.details-title {
	color: rgba(0, 0, 0, .4);
	text-transform: uppercase;
	font-size: 12px;
}

.details-slider {
	margin: 0;
	padding: 0;
}

.details-item {
	color: #222;
	list-style: none;
	padding: 0 10px;
	margin-bottom: 50px;
}

.details-item__headline {
	display: flex;
	justify-content: space-between;
	font-size: 20px;
	margin-bottom: 5px;
}

.details-item__time {
	font-weight: 500;	
}

.details-item__cost {
	font-weight: 600;
	color: rgb(51, 114, 226);
}

.details-item__secondary {
	color: rgba(0, 0, 0, .4);
	font-size: 12px;
	padding: 0;
	margin: 0;
	margin-bottom: 5px;
	text-align: left;
}
.App {
  text-align: center;
}


